@mixin burger {
  display: block;
  width: rem(30px);
  height: rem(18px);
  position: relative;
  z-index: 51;
  cursor: pointer;
  span {
    transition: all 0.3s ease 0s;
    position: absolute;
    top: calc(50% - 1px);
    right: 0;
    width: 100%;
    height: rem(2px);
    background: #fff;
    border-radius: 2px;
   
    @include fch() {
      top: 0;
    }
    @include lch() {
      width: 100%;
      top: auto;
      bottom: 0;
    }
  }
  &.active {
    span {
      transform: scale(0);
      @include fch() {
        transform: rotate(-45deg);
        top: calc(50% - 1px);
      }
      @include lch() {
        width: 100%;
        transform: rotate(45deg);
        bottom: calc(50% - 1px);
      }
    }
  }
}
