.fixed-header {
  background: $black;
}

.toptitle {
  text-transform: uppercase;
  color: $muleFawn;
  @include font-size(13, 1, 5);
}

.title {
  text-transform: uppercase;
  color: $white;
}

.title-2 {
  text-transform: uppercase;
  color: $white;
  font-size: #{clamp-size(45px, 55px, 320px, 1150px)};
  margin-bottom: rem(30px);
}

.text {
  font-family: $secondFontFamily;
  color: $dustyGray;
  @include font-size(15, 28);
}

.text-stroke {
  color: transparent;
  @include font-stroke(1px, $muleFawn);
}

.name {
  text-transform: uppercase;
  color: $white;
  @include font-size(27, 27, 3);
}



.btn {
  display: inline-block;
  overflow: hidden;
  cursor: pointer;
  text-transform: capitalize;
  font-family: $secondFontFamily;
  font-size: rem(15px);
  padding: rem(12px) rem(24px);
  color: $white;
  background: $muleFawn;
  @include before () {
    @include transition(0.3);
    width: 100%;
    height: 100%;
    background: $white;
    left: 0;
    top: rem(-45px);
  }
  @include e (text-two) {
    position: absolute;
    bottom: rem(-22px);
  }
}

input[type="text"],
input[type="email"],
input[type="password"],
input[type="tel"],
input[type="url"],
input[type="file"],
input[type="number"],
input[type="date"] {
  color: $white;
  font-size: rem(15px);
  display: inline-flex;
  height: rem(56px);
  width: 100%;
  line-height: 34px;
  font-weight: 400;
  padding: 10px 20px;
  background-color: $codGray;
  @include borderopacity(1px, $white, 0.2);
  @include focus() {
    outline: none;
  }
}


.swiper-pagination-bullet {
  width: rem(12px);
  height: rem(12px);
  @include border (2px, $white);
}

.swiper-pagination-bullet-active {
  background: $white;
}