.about {
  @include after(2, 90) {
    background: $muleFawn;
    bottom: rem(-45px);
    left: 50%;
    z-index: 5;
  }
  @include e(container) {
    padding-top: rem(120px);
    padding-bottom: rem(120px);
  }
  @include e(content) {
    margin-bottom: rem(20px);
  }
  @include e(toptitle) {
    margin-bottom: rem(10px);
  }
  @include e(text) {
    margin-bottom: rem(15px);
  }
  @include e(item) {
    z-index: -1;
    @include before() {
      content: "\f00c";
      color: $muleFawn;
      font-family: "FontAwesome";
      font-size: rem(15px);
      left: 0;
      top: 0;
    }
    padding-left: rem(20px);
    @include notlc() {
      margin-bottom: rem(10px);
    }
  }
  @include e(img) {
    @include img();
  }
}


