.appointment {
  @include after(2, 90) {
    background: $muleFawn;
    bottom: rem(-45px);
    left: 50%;
    z-index: 1;
  }
  @include before() {
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    @include bg-opacity($black, 0.5);
  }
  @include bg-img("../img/apointment.jpg", center);
  background-attachment: fixed;
  @include e(container) {
    padding-top: rem(120px);
    padding-bottom: rem(120px);
    position: relative;
    z-index: 2;
  }
  @include e(content) {
    margin-bottom: rem(30px);
  }
  @include e(input) {
    @include notlc() {
      margin-bottom: rem(30px);
    }
    @include m(arrow) {
      cursor: pointer;
    }
  }
  @include e(toptitle) {
    margin-bottom: rem(10px);
  }
  @include e(text) {
    color: $white;
    margin-bottom: rem(30px);
  }
  @include e(form) {
    padding: rem(20px);
    background: $codGray;
  }
  @include e(phone) {
    display: block;
    cursor: pointer;
    padding-left: rem(60px);
    @include before() {
      content: "\f2a0";
      font-size: rem(45px);
      font-family: "FontAwesome";
      color: $white;
      left: 0;
      top: 0;
    }
  }
}

.phone {
  @include e(link) {
    color: $white;
    font-size: rem(27px);
    font-weight: $medium;
  }
  @include e(toptitle) {
    margin-bottom: rem(10px);
  }
}

.select {
  @include e(menu) {
    position: relative;
    cursor: pointer;
    color: $dustyGray;
    font-family: $secondFontFamily;
    font-size: rem(15px);
    height: rem(56px);
    width: 100%;
    line-height: 34px;
    font-weight: 400;
    padding: 10px 20px;
    background-color: $codGray;
    @include borderopacity(1px, $white, 0.2);
    @include focus() {
      outline: none;
    }
    @include notlc() {
      margin-bottom: rem(30px);
    }
  }

  @include e(btn) {
    @include flex-sb();
    @include after() {
      content: "";
      top: 45%;
      right: 0;
      width: rem(10px);
      height: rem(10px);
      border-right: 2px solid $white;
      border-bottom: 2px solid $white;
      transform: translateY(-50%) rotate(45deg);
      @include transition(0.3);
    }
    &.active {
      @include after() {
        width: rem(10px);
        height: rem(10px);
        transform: translateY(-50%) rotate(225deg);
      }
    }
  }
  @include e(icon) {
    color: $white;
    font-size: rem(18px);
    @include transition(0.3);
  }
  @include e(options) {
    opacity: 0;
    max-height: 0;
    overflow: hidden;
    position: absolute;
    top: rem(55px);
    left: 0;
    width: 100%;
    z-index: 8;
    text-align: center;
    background: $tundora;
    margin-bottom: rem(30px);
    cursor: pointer;
    font-family: $secondFontFamily;
    color: $dustyGray;
    @include font-size(18, 50);
    @include transition(0.3);
    &.active {
      opacity: 1;
      max-height: inherit;
    }
  }
}
