.services {
  @include e(container) {
    padding-top: rem(120px);
    padding-bottom: rem(120px);
  }
  @include e(toptitle) {
    margin-bottom: rem(5px);
  }
  @include e(card) {
    @include notlc() {
      margin-bottom: rem(50px);
    }
  }
  @include e(img) {
    @include img();
    margin-bottom: rem(30px);
  }
  @include e(name) {
    margin-bottom: rem(15px);
  }
  @include e(num) {
    display: none;
    font-size: rem(70px);
    font-weight: $medium;
    text-transform: uppercase;
    color: transparent;
    @include font-stroke(1px, $muleFawn);
  }
  @include e(text) {
    margin-bottom: rem(20px);
  }
}

