.team {
  @include e(content) {
    text-align: center;
  }
  @include e(slide) {
    padding-bottom: 50px;
  }
  @include e(inner) {
    @include flex-sb();
  }
  @include e(img) {
    position: relative;
    flex: 0 0 70%;
  }
  @include e(info) {
    padding: rem(60px) rem(45px);
    background: $codGray;
    max-width: rem(600px);
    position: absolute;
    top: 50%;
    right: rem(-90px);
    transform: translateY(-43%);
    z-index: 2;
  }
}

.swiper-pagination {
  bottom: -30px;
}
