.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 50;
  @include e(container) {
    padding-top: #{clamp-size(20px, 25px, 320px, 1150px)};
    padding-bottom: #{clamp-size(20px, 25px, 320px, 1150px)};
  }
  @include e(nav) {
    @include flex-sb();
  }
}

.nav {
  @include e(logo) {
    max-width: rem(140px);
    @include img();
  }
  @include e(menu) {
    text-align: center;
    padding: rem(25px) 0 rem(25px) rem(15px);
    position: absolute;
    top: rem(93px);
    left: -100%;
    width: 100%;
    background: $black;
    z-index: 100;
    @include transition(.3);
    &.active {
        left: 0;
    }
  }
  @include e(item) {
    padding: rem(8px) 0;
  }
  @include e(link) {
    @include font-size(15, 1, 2);
    text-transform: uppercase;
    color: $white;
  }
}

.burger {
  @include burger();
}
