.clients {
  @include before() {
    width: 100%;
    height: 100%;
    background: $black;
    opacity: 0.5;
    z-index: 0;
  }
  @include bg-img("../img/apointment.jpg", center);
  background-attachment: fixed;
  @include e(container) {
    padding-top: rem(120px);
    padding-bottom: rem(120px);
  }
  @include e(img) {
    overflow: hidden;
    width: rem(60px);
    height: rem(60px);
    border-radius: 50%;
    @include img();
  }
  @include e(slide) {
    padding: rem(35px) rem(20px);
    background: $codGray;
  }
  @include e(info) {
    @include before() {
      width: rem(1px);
      height: 100%;
      background: $muleFawn;
      top: 0;
      left: 0;
    }
    padding-left: rem(15px);
    margin-bottom: rem(30px);
  }
  @include e(quote) {
    position: absolute;
    right: rem(34px);
    top: rem(140px);
    width: rem(70px);
    opacity: 0.3;
  }
  @include e(person) {
    @include flex();
  }
  @include e(about) {
    font-family: $secondFontFamily;
    padding-left: rem(15px);
  }
  @include e(name) {
    @include font-size(15, 15);
    color: $white;
    margin-bottom: rem(5px);
  }
  @include e(review) {
    @include font-size(13, 13);
    color: $dustyGray;
  }
}

.brands {
  padding: rem(20px) 0;
  background: $black;
}

.brands__slide {
  @include img();
}
.brands__img {
  width: rem(97px);
  height: rem(64px);
}

.modal {
  @include e(video) {
    @include flex();
    gap: rem(20px);
    position: relative;
    z-index: 1;
    cursor: pointer;
    margin-bottom: rem(50px);
  }
  @include e (icon) {
    color: $white;
    font-family: "Font Awesome";
    font-size: rem(50px);
  }
  @include e(box) {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 99999;
    @include bg-opacity($codGray, 0.9);
    @include flex-c();
    opacity: 0;
    pointer-events: none;
    @include transition(0.3);
    &.active {
      opacity: 1;
      pointer-events: visible;
    }
  }
  @include e(close) {
    cursor: pointer;
    color: $white;
    font-size: rem(50px);
    position: absolute;
    top: rem(50px);
    @include transition(0.2);
  }
}

iframe {
  width: rem(1000px);
  height: rem(500px);
}
