.process {
  @include after(2, 90) {
    background: $muleFawn;
    bottom: rem(-45px);
    left: 50%;
    z-index: 5;
  }
  @include e(container) {
    padding-top: rem(120px);
    padding-bottom: rem(120px);
  }
  @include e(content) {
    margin-bottom: rem(30px);
  }
  @include e(toptitle) {
    margin-bottom: rem(10px);
  }

  @include e(item) {
    padding-left: rem(20px);
    @include before() {
      content: "\f068";
      color: $muleFawn;
      font-family: "FontAwesome";
      font-size: rem(15px);
      left: 0;
      top: 0;
    }
  }
}

.accordion {
  @include e(title) {
    cursor: pointer;
    @include before() {
      content: "\2b";
      right: 0;
      top: 50%;
      transform: translateY(-50%);
      font-family: "FontAwesome";
      font-weight: 300;
      @include color-opacity($white, 0.2);
    }
    padding: rem(20px) 0;
    text-transform: uppercase;
    color: $white;
    @include font-size(18, 21, 2);
    @include notlc() {
      @include borderopacity-b(1px, $white, 0.2);
    }
    &.active {
      color: $muleFawn;
      @include transition(0.3);
      @include before() {
        content: "\f068";
        right: 0;
        top: 50%;
        transform: translateY(-50%);
        font-family: "FontAwesome";
        font-weight: 300;
        @include color-opacity($white, 0.2);
      }
    }
  }
  @include e(box) {
    overflow: hidden;
    max-height: 0;
    @include transition(0.3);
  }
}
