.nav {
  @include e(link) {
    @include transition(0.3);
    @include hover() {
      color: $muleFawn;
    }
  }
}

.btn {
  @include hover() {
    color: $muleFawn;
    @include before() {
      top: 0;
    }
  }
}

.btn:hover .btn__text-two {
  @include transition(0.3);
  bottom: rem(12px);
}

.footer {
  @include e(link) {
    @include transition(0.3);
    @include hover() {
      color: $muleFawn;
    }
  }
}

.select {
  @include e(option) {
    @include transition(0.3);
    @include hover() {
      color: $white;
      background: $muleFawn;
    }
  }
}

.select__option:hover .select__text {
  color: $white;
}

.modal {
  @include e(close) {
    @include hover() {
      transform: rotate(90deg);
    }
  }
}

.modal__icon {
  @include before() {
    top: rem(2px);
    bottom: 0;
    width: rem(50px);
    height: rem(50px);
    @include border(5px, $white);
    // background: green;
    border-radius: 50%;
    opacity: 0.5;
    animation: ripple 3s linear infinite;
  }
  @include after() {
    top: rem(12px);
    left: rem(10px);
    width: rem(30px);
    height: rem(30px);
    background: $white;
    border-radius: 50%;
    opacity: 0.5;
    animation: ripple 3s linear infinite;
  }
}

@keyframes ripple {
  100% {
    transform: scale(2);
    opacity: 0;
  }
}
