html {
    font-size: $baseFontSize;
  }
  html,
  body {
    &.lock {
      overflow: hidden;
    }
    font-family: $baseFontFamily;
    font-size: 1rem;
    font-weight: $regular;
    background: $codGray;
  }
  
  a {
    text-decoration: none;
  }
  
  li,
  ul,
  p {
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  h1,
  h2,
  h3,
  h4,
  h5,
  h6 {
    margin: 0;
  }
  
  /* ------------------------------------------------------------------ */
  
  [class*="__container"] {
    @include container (1150px);
  }
  