.blog {
  @include e(container) {
    padding-top: rem(120px);
    padding-bottom: rem(120px);
  }
  @include e(info) {
    text-align: center;
    margin-bottom: rem(30px);
  }
  @include e(toptitle) {
    margin-bottom: rem(5px);
  }
  @include e(img) {
    @include img();
  }
  @include e(content) {
    position: relative;
    left: 0;
    top: rem(-50px);
    padding: rem(30px) rem(20px);
    margin: 0 rem(15px);
    background: $black;
    z-index: 5;
  }
  @include e(name) {
    display: block;
    text-transform: uppercase;
    color: $white;
    @include font-size(24, 28.8, 3);
    margin-bottom: rem(30px);
  }
  @include e(date) {
    @include flex();
    font-family: $secondFontFamily;
    @include font-size(13, 26.25);
  }
  @include e(article) {
    @include after(4, 4) {
      top: 50%;
      left: rem(30px);
      transform: translateY(-50%);
      @include bg-opacity($muleFawn, 0.5);
      border-radius: 50%;
    }
    margin-right: rem(13px);
    color: $muleFawn;
  }
  @include e(month) {
    color: $dustyGray;
  }
}
