.banner {
  @include after(2, 90) {
    background: $muleFawn;
    bottom: rem(-45px);
    left: 50%;
    z-index: 5;
  }
  @include before() {
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    @include bg-opacity($black, 0.5);
  }
  height: 100vh;
  @include bg-img("../img/banner.jpg", center);
  background-attachment: fixed;
  @include e(container) {
    @include flex-c();
    flex-direction: column;
    height: 100vh;
  }
  @include e(content) {
    text-align: center;
    z-index: 2;
  }
  @include e(toptitle) {
    margin-bottom: rem(15px);
  }
  @include e(title) {
    font-size: #{clamp-size(55px, 75px, 320px, 1150px)};
    span {
      color: transparent;
      @include font-stroke(0.5px, $white);
    }
  }
  @include e(btn) {
    display: inline-block;
    margin-top: rem(30px);
  }
}
