.team {
  @include after(2, 90) {
    background: $muleFawn;
    bottom: rem(-45px);
    left: 50%;
    z-index: 5;
  }
  @include e(container) {
    padding-top: rem(120px);
    padding-bottom: rem(120px);
  }
  @include e(toptitle) {
    display: initial;
    margin-bottom: rem(10px);
  }
  @include e(text) {
    margin-bottom: rem(15px);
  }
  @include e(img) {
    @include img();
  }
  @include e(info) {
    padding: rem(45px) rem(20px);
  }
  @include e(name) {
    margin-bottom: rem(5px);
  }
  @include e(job) {
    text-transform: capitalize;
    color: $muleFawn;
    font-family: $secondFontFamily;
    font-size: rem(15px);
    margin-bottom: rem(20px);
  }
  @include e(btn) {
    display: inline-block;
  }
}
