.footer {
  @include e(container) {
    padding-top: rem(120px);
    padding-bottom: rem(120px);
  }
  @include e(info) {
    margin-bottom: rem(30px);
  }
  @include e(title) {
    margin-bottom: rem(15px);
  }
  @include e(text) {
    margin-bottom: rem(15px);
  }
  @include e(phone) {
    display: block;
    margin-bottom: rem(15px);
  }
  @include e(mail) {
    display: block;
    margin-bottom: rem(15px);
  }
  @include e(social) {
    @include flex();
    gap: 15px;
  }
  @include e(link) {
    color: $white;
  }
  @include e(item) {
    position: relative;
    @include flex-sb();
  }
  @include e(day) {
    background: $codGray;
    padding-right: rem(10px);
  }
  @include e(dots) {
    position: absolute;
    height: rem(5px);
    width: 80%;
    border-bottom: 1px dashed rgba(255, 255, 255, 0.1);
    z-index: -1;
  }
  @include e(time) {
    background: $codGray;
    padding-left: rem(10px);
  }
  @include e(input) {
    @include border(2px, $muleFawn !important);
  }
  @include e(form) {
    position: relative;
  }
  @include e(btn) {
    position: absolute !important;
    top: 9px;
    right: 8px;
  }
  @include e(copyright) {
    @include borderopacity-t(1px, $white, 0.1);
    padding: rem(32px) 0;
    text-align: center;
    @include font-size(13, 22);
  }
  @include e(link-2) {
    color: $muleFawn;
  }
}
