.price {
  @include e(content) {
    padding-top: rem(120px);
    padding-bottom: rem(120px);
  }
  @include e (inner) {
    background: $black;
  }
  @include e(img) {
    @include img();
  }
  @include e(info) {
    padding: rem(45px) rem(15px);
  }
  @include e(toptitle) {
    margin-bottom: rem(5px);
  }
  @include e(item) {
    padding: rem(15px) 0;
    @include flex-sb();
    font-family: $secondFontFamily;
    color: $dustyGray;
    @include borderopacity-b (1px, $white, 0.2);
  }
}

