// --------------------------------- BEFORE, AFTER--------------------------------- //

@mixin before($w: none, $h: none) {
  & {
    position: relative;
  }

  &::before {
    content: "";
    position: absolute;
    width: #{$w}px;
    height: #{$h}px;
    @content;
  }
}

@mixin after($w: 0, $h: 0) {
  & {
    position: relative;
  }

  &::after {
    content: "";
    position: absolute;
    width: #{$w}px;
    height: #{$h}px;
    @content;
  }
}
